import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import breakpoint from 'styled-components-breakpoint'

import ContactForm from '../components/ContactForm.js'
import {Button, PhotoWrapper, PageHeader} from '../utils/styled-components.js'

//styles


const PhotoWrapperContact = PhotoWrapper.extend`
  background-image:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/s--Apiy0pSL--/c_scale,q_jpegmini,w_3773/v1493834434/london_f0prw6.jpg');
`

const Label = styled.label`

`
const ContactWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
`


const ContactPage = ({data}) => (
  <ContactWrapper>
    <PhotoWrapperContact/>
    <PageHeader>Contact</PageHeader>
    <h4 style={{textAlign: 'center', color: "white", lineHeight: "1.2rem"}}>
    Interested in inviting us to your school or business?
      <br/>
    Reach out to us below.
      <br/>
    We'd be happy to hear from you!
    </h4>
    <ContactForm contactEmail={data.site.siteMetadata.email} />
    <Link to="/page-2/"><Button>Register Today</Button></Link>
  </ContactWrapper>
)
export const query = graphql`
  query ContactQuery{
    site{
      siteMetadata{
        email
      }
    }
  }
`

export default ContactPage



